@import './Variables.scss';


.creator{
    @include maxWidthmaxHeight(100%,auto);
    
    .creator-title{
        @include maxWidthmaxHeight(100%,100px);
        background: rgb(255, 255, 255);
        @include displayFlex(row);
        @media(max-width:768px){
            @include displayFlex(column);
            @include maxWidthmaxHeight(100%,150px);
        }
        p{
            position: relative;
            @include maxWidthmaxHeight(30%,50%);
            @include top50;
            @include paddingMargin(5px,0);
            text-transform: uppercase;
            font-size: 1.5rem;
            color:rgb(173, 173, 173);
            text-align: center;
            @media(max-width:768px){
                @include maxWidthmaxHeight(100%,50%);
                top:0%;
                transform: translateY(0);
                @include paddingMargin(10px,0);
            }
            @media(max-width:300px){
                font-size: 0.9rem;
            }
        }
        .creator-list{
            position: relative;
            @include maxWidthmaxHeight(70%,100%);
            background: rgb(63, 7, 168);
            @media(max-width:768px){
                @include maxWidthmaxHeight(100%,50%);
            }
            ul{
                @include maxWidthmaxHeight(100%,auto);
                list-style-type: none;
                @include displayFlex(row);
                @include top50;
                justify-content: center;
                li{
                    color: white;
                    margin-left:10px;
                    margin-right:10px;
                    cursor: pointer;
                    &:hover{
                        color:red;
                    }
                    @media(max-width:768px){
                        font-size: 0.8rem;
                    }
                    @media(max-width:300px){
                        font-size: 0.7rem;
                    }
                }
            }
        }
    }
    .creator-content{
        @include maxWidthmaxHeight(100%,auto);
        @include displayFlex(row);
        @media(max-width: 768px){
            @include displayFlex(column);
        }
        .creator-pattern{
            @include maxWidthmaxHeight(35%,auto);
            box-shadow: 0 0 5px rgb(195, 195, 195);
            @include paddingMargin(0,0);
            @media(max-width: 768px){
                @include maxWidthmaxHeight(100%,auto); 
            }
            .creator-pattern-title{
                @include maxWidthmaxHeight(100%,80px);
                background: rgb(67, 5, 117);
                p{
                    text-align: center;
                    color:rgb(223, 223, 223);
                    @include top50;
                    font-size: 1.5rem;
                    @media(max-width: 768px){
                        font-size: 1rem;
                    }
                }
            }
            .creator-pattern-sizes{
                @include maxWidthmaxHeight(100%,150px);
                box-shadow: 0 0 5px rgb(207, 207, 207);
                p{
                    @include maxWidthmaxHeight(100%,50px); 
                    font-size:1.5rem;
                    padding-top: 5px;
                    text-align: center;
                    color:rgb(148, 148, 148);
                    @media(max-width: 768px){
                        font-size: 1rem;
                    }
                }
                ul{
                    @include maxWidthmaxHeight(100%,auto);
                    list-style-type: none;  
                    @include displayFlex(row);
                    padding:0;
                    justify-content: center;
                    li{
                        width:60px;
                        height:50px;
                        box-shadow: 0 0 5px rgb(156, 156, 156);
                        border-bottom: 2px solid rgb(104, 0, 0);
                        @media(max-width: 992px){
                            @include maxWidthmaxHeight(55px,55px);  
                        }
                        p{  
                            background-color: white;
                            @include maxWidthmaxHeight(100%,100%);
                            color:rgb(121, 121, 121);
                            padding: 10px;
                            font-size: 1.2rem;
                            @media(max-width: 992px){
                                font-size: 1.1rem;  
                            }
                        }
                    }
                }
            }
            button{
                @include maxWidthmaxHeight(150px,80px);
                @include paddingMargin(0,10px auto);
                background: rgb(253, 253, 253);
                box-shadow: 0 0 10px rgb(175, 175, 175);
                color:rgb(60, 10, 124);
                border: none;
                outline: none;
                text-transform: uppercase;
                display: none;
                @media(max-width: 768px){
                    display:block;
                    @include maxWidthmaxHeight(130px,60px);
                    font-size: 0.9rem;
                }
            }
            .creator-pattern-content{
                @include maxWidthmaxHeight(100%,auto);
                .color-pattern{
                    @include maxWidthmaxHeight(100%,auto);
                    padding: 5px;
                    @include displayFlex(row);
                    justify-content: center;
                    .color-pattern-element{
                        @include maxWidthmaxHeight(50px,50px); 
                        margin: 5px;
                        cursor: pointer;
                        transform: skew(-20deg);
                        box-shadow: 0 0 5px rgb(0, 0, 0);
                        transition: all 0.2s;
                        @media(max-width: 992px){
                            @include maxWidthmaxHeight(40px,40px);  
                        }
                        &:hover{
                            transform: scale(1.1);
                        }
                    }
                }
                .theme-pattern{
                    background: linear-gradient(to bottom,black,silver);
                    display: block;
                    position: relative;
                    @include maxWidthmaxHeight(100%,90%);
                    overflow:scroll;
                    .theme-pattern-element{
                        width: 100%;
                        height: 200px;
                        background: rgba(0, 0, 0, 0.555);
                        margin-top: 5px;
                        color:white;
                        text-align: center;
                        cursor: pointer;
                        @media(max-width: 768px){
                            @include maxWidthmaxHeight(50%,150px); 
                        }
                    }
                }
            }
        }
        .creator-draw{
            position: relative;
            @include maxWidthmaxHeight(65%,auto);
            @media(max-width:768px){
                @include maxWidthmaxHeight(100%,auto);
            }
            .tshirt{
                position: relative;
                @include maxWidthmaxHeight(500px,600px);
                left: 50%;
                transform: translateX(-50%);
                margin-top: 10%;
                background: url('../img/tshirt.png');
                background-size: cover;
                background-position: top;
                border: 1px solid black;
                @media(max-width:1200px){
                    @include maxWidthmaxHeight(400px,500px);
                }
                @media(max-width:400px){
                    @include maxWidthmaxHeight(100%,400px);
                }
                .tshirt-content{
                    width: 50%;
                    height: 60%;
                    margin: 25% auto;
                    .tshirt-content-element{
                        position: relative;
                        @include maxWidthmaxHeight(100%,auto);
                        text-align: center;
                        cursor: move;
                        p{
                            font-size: 2rem;
                            color:rgb(3, 0, 0);
                            text-shadow: 0 0 5px silver;
                        }
                    }
                }
            }
        }
    }
}